.audio-module-title {
    font-size: 28px;
    line-height: 36px;
    font-style: italic;
    margin: 4px 16px;
}

.result-audio-row {
    margin: 0 12px 4px;
    padding: 4px;
    border: 2px dashed var(--grey-dark);
}

.audio-player {
    cursor: pointer;
    height: 24px;
    width: 24px;
    padding: 4px;
    background-color: var(--grey);
}

.result-audio-speaker {
    color: var(--grey-darker);
    font-size: 24px;
    line-height: 28px;
    margin: 2px 0 2px 12px;
}

.result-audio-subtitle {
    margin: 8px 8px 4px;
    font-size: 16px;
    line-height: 20px;
    color: #c3cfd9;
    font-style: italic;
}
